import React from 'react';
import SubLayout from '../components/SubLayout';
import '../styles/era2010sub.css';

const Era2010sub = () => {
  return (
    <SubLayout
      styleType="era2010sub"
      headerStyleType="era2010sub"
      use3D={true} //   true
      modelPath="/m10odels/scene.gltf"
      modelScale={16}
      texts={[
        '> : 2010s',
        '> :  , ,  UI',
        '> :  (UX)    .',
      ]}
    />
  );
};

export default Era2010sub;