import React from 'react';
import SubLayout from '../components/SubLayout';
import '../styles/era1990sub.css';

const Era1990sub = () => {
  return (
    <SubLayout
      styleType="era1990sub" //    
      headerStyleType="era1990sub" //       
      use3D={true} //   true
      modelPath="/m95odels/scene.gltf"
      modelScale={1}
      texts={[
        '> : 1990s',
        '> : 95, ,  gif',
        '> :   ,  ',
      ]}
    />
  );
};

export default Era1990sub;
