import React from 'react';
import SubLayout from '../components/SubLayout';
import '../styles/era1980sub.css';

const Era1980sub = () => {
  return (
    <SubLayout
      styleType="era1980sub"
      headerStyleType="era1980"
      use3D={true} //   true
      modelPath="/m80odels/scene.gltf"
      texts={[
        '> : 1980s',
        '> : , , ',
        '> : 1980      ',
      ]}
    />
  );
};

export default Era1980sub;
