import React from 'react';
import SubLayout from '../components/SubLayout';
import '../styles/era2000sub.css';

const Era2000sub = () => {
  return (
    <SubLayout
      styleType="era2000sub"
      headerStyleType="era2000sub" //     
      use3D={true} //   true
      modelPath="/m00odels/scene.gltf"
      modelScale={6}
      texts={[
        '> : 2000s',
        '> : XP, MSN,  UI',
        '> :    , XP    ',
      ]}
    />
  );
};

export default Era2000sub;
